import { ResponsiveLine } from '@nivo/line';
import {tokens} from "../theme";
import {useTheme} from "@mui/material";
import {  useEffect, useState} from 'react';
import { processData, formatTime } from '../dataProcessing';

const LineChart = ({data, lineColor}) => {
    const [stockData, setStockData] = useState([]);
    console.log("Line chart data:", data);
    const formatTime = (timestamp) => {
      const date = new Date(timestamp);
      const hours = date.getHours().toString().padStart(2, '0');
      const minutes = date.getMinutes().toString().padStart(2, '0');

      return `${hours}:${minutes}`;
    };



    useEffect(() => {
      const fetchData = async (searchValue) => {
        console.log('Updated Search value:', searchValue);
        try {
          const response = await fetch(`http://localhost:5000/stocks?symbol=${searchValue}`);
          const jsonData = await response.json();
          console.log('Fetched JSON data:', jsonData);
          const processedData = processData(jsonData);
          console.log("Processed data:", processedData);
          setStockData(processedData);
        } catch (error) {
          console.error('Error fetching data:', error);
        }
      };

  fetchData(data);
}, [data]);


    const theme = useTheme();
    const colors = tokens(theme.palette.mode);
    return (

        <ResponsiveLine

            data={data}
            theme={{
                axis:{
                    domain:{
                        line:{
                            stroke: colors.grey[100]
                        }
                    },
                    legend:{
                        text:{
                            fill:colors.grey[100]
                        }
                    },
                    ticks:{
                        line:{
                            stroke: colors.grey[100],
                            strokeWidth: 1
                        },
                        text:{
                            fill: colors.grey[100]
                        }
                    }
                },
                legends:{
                    text:{
                        fill: colors.grey[100]
                    },
                },
                tooltip: {
                    container: {
                        color: colors.primary[500]
                    }
                }
            }}
            margin={{top: 50, right: 110, bottom: 95, left: 70}}
            xScale={{type: 'point'}}
            yScale={{
                type: 'linear',
                min: 'auto',
                max: 'auto',
                stacked: true,
                reverse: false
            }}
            yFormat=" >-.2f"
            curve="catmullRom"
            axisTop={null}
            axisRight={null}
            axisBottom={{
                orient: 'bottom',
                tickSize: 5,
                tickPadding: 20,
                tickRotation: -45,
                legend: '',
                legendOffset: 36,
                legendPosition: 'middle'
            }}
            axisLeft={{
                orient: 'left',
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '',
                legendOffset: -40,
                legendPosition: 'middle'
            }}
            enableGridX={false}
            enableGridY={false}
            colors={ lineColor }
            pointSize={10}
            pointColor={{theme: 'background'}}
            pointBorderWidth={2}
            pointBorderColor={{from: 'serieColor'}}
            pointLabelYOffset={-12}
            useMesh={true}
            legends={[
                {
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 100,
                    translateY: 0,
                    itemsSpacing: 0,
                    itemDirection: 'left-to-right',
                    itemWidth: 80,
                    itemHeight: 20,
                    itemOpacity: 0.75,
                    symbolSize: 12,
                    symbolShape: 'circle',
                    symbolBorderColor: 'rgba(0, 0, 0, .5)',
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemBackground: 'rgba(0, 0, 0, .03)',
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
        />
    )
};
export default LineChart;